body {
  margin: 0;
  padding: 0;
  text-align: center;
}

p {
  margin: 0;
  padding: 0;
}

.header {
  margin: 0;
  margin-bottom: 20px;
  width: 100%;
  text-align: center;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.651);
}

.nav {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.link {
  width: 150px;
  margin: 0;
  display: inline-block;
  color: #000;
  text-decoration: none;
  padding: 20px;
  background-color: #fff;
  transition: background-color 300ms ease;
}

.link:hover {
  background-color: rgb(203, 189, 255);
}

.activeLink {
  background-color: rgb(218, 209, 255);
}
